.post {
	height: auto;
	&__form {
		text-align: center;
	}
	&__title {
		color: $black;
		font-family: $opensans;
		font-size: 42px;
		font-weight: $regular;
		text-align: center;
		margin-bottom: 20px;
		@include overcontent;
	}
	&__caption {
		color: #010000;
		font-family: $opensans;
		font-size: 18px;
		font-weight: $regular;
		text-align: center;
		margin-bottom: 60px;
		@include overcontent;
	}
	&__box {
		display: flex;
		justify-content: space-between;
		margin-bottom: 40px;
	}
	&__item {
		position: relative;
		width: 100%;		
		max-width: 360px;
		height: 56px;
		&:first-child {
			margin-right: 30px;
		}
		&:last-child {
			margin-left: 30px;
		}
	}
	&__label {
		text-align: center;
		padding: 0 5px;
		color: #8d8c8c;
		font-family: $opensans;
		font-size: 12px;
		font-weight: $regular;
		background-color: #fff;
		z-index: 999;
		position: absolute;
		top: -5px;
		left: 20px;
	}
	&__input {
		height: 100%;
		width: 100%;
		color: $black;
		background-color: $white;
		font-family: $opensans;
		font-size: 16px;
		font-weight: $regular;
		border-radius: 4px;
		border: 1px solid #b7b7b7;
		text-indent: 17px;
		&::placeholder {
			color: #8d8c8c;
			background-color: $white;
			font-family: $opensans;
			font-size: 16px;
			font-weight: $regular;
		}
	}
	&__position {
		height: 56px;
		width: 100%;
		max-width: 555px; 
		position: relative;
		&:hover {
			.post__icon {
				top: 36%;
				transform: rotate(180deg);
			}
		}
	}
	&__sel {
		cursor: pointer;
		background: transparent;
		width: 100%;
		height: 100%;
		color: $black;
		font-family: $opensans;
		font-size: 16px;
		font-weight: $regular;
		border-radius: 4px;
		border: 1px solid #b7b7b7;
		text-indent: 17px;
		margin-right: 10px;
		position: relative;
		appearance:none;
	}
	&__icon {
		position: absolute;
		top: 50%;
		right: 15px;
		transform: translateY(-50%);
		transition: all $time;
		
	}
	&__file {
		position: absolute;
		right: 0;
		top: 0;
		width: 30%;
		height: 100%;
		opacity: 0;
	}
	&__upload {
		position: relative;
		max-width: 555px;
		width: 100%;
		height: 56px;
		border-radius: 4px;
		border: 1px solid #b7b7b7;
		margin-left: 30px;
	}
	&__btn {
		position: absolute;
		right: -1px;
		top: -1px;
		width: 30%;
		height: 56px;
		color: #ef6c00;
		font-family: $opensans;
		font-size: 16px;
		background-color: #fff;
		font-weight: $bold;
		border: 2px solid #ef6c00;
		border-radius: 0 4px 4px 0;
		cursor: pointer;
		@include overcontent;
	}
	&__name {
		position: absolute;
		left: 20px;
		top: 50%;
		transform: translateY(-50%);
		font-family: $opensans;
		font-size: 16px;
		font-weight: $regular;
		color: #8d8c8c;
	}
	&__down {
		top: 60px;
	}
	&__submit {
		@include overcontent;
		max-width: 290px;
		width: 100%;
		margin: 0 auto;
		height: 40px;
		border-radius: 4px;
		background-color: #d7d7d7;
		margin-bottom: 50px;
		color: #8d8c8c;
		font-weight: $bold;
	}
	&__error {
		position: absolute;
		bottom: -20px;
		left: 5px;
		color:#f44336;
		font-family: $opensans;
		font-size: 12px;
		font-weight: $regular;
		background-color: #fff;
		@include overcontent;
	}
	&__red {
		border: 1px solid #f44336;
		&:focus {
			border: none;
			border-radius: 5px;
			outline: #f44336 auto 2px;
		}
	}
	&__label-error {
		color:#f44336;
	}
	&__option {
		cursor: pointer;
		height: 47px;
		transition: all $time;
		&:hover {
			background-color: #fce2cc;
		}
	}
}

.footer {
	background-color: #283593;
	position: relative;
	&::before {
